	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Otitis Media","date":"06 Jan 2004","slug":"/2004-01-06-otitis-media/","tags":"Y2004"},"html":"<p>As the first consequence of our visit with the <a title=\"PapaScott : Speech Therapy\" href=\"http://www.papascott.de/2003/12/23/2769.php\">speech therapist</a> last month, we took Christopher to a ear-nose-throat specialist today. He told us what we already suspected: Christopher has fluid behind the eardrums in both ears, his adenoids are enlarged, and he is a classic candidate for an adenoidectomy. I was surprised that he recommended surgery right away (I had a similar operation at age 8), but doing some reading I learned that such a procedure at age 4 is not unusual, and these days can be done as an out-patient. We've made an appointment for the end of the month. We also had a hearing test done, but since Christopher was somewhat uncooperative, the results were inconclusive. He only hears what he wants to hear. Don't we all?</p>"},"next":{"fields":{"title":"Federal Voting Assistance Program (FVAP)","date":"06 Jan 2004","slug":"/2004-01-06-federal-voting-assistance-program-fvap/"}},"previous":{"fields":{"title":"ARD Tagesschau: News in English","date":"07 Jan 2004","slug":"/2004-01-07-ard-tagesschau-news-in-english/"}}},{"node":{"fields":{"title":"Federal Voting Assistance Program (FVAP)","date":"06 Jan 2004","slug":"/2004-01-06-federal-voting-assistance-program-fvap/","tags":"Y2004"},"html":"<p><a title=\"Absentee ballot information and applications for overseas citizens\" href=\"http://www.fvap.gov/\">Federal Voting Assistance Program (FVAP)</a></p>"},"next":{"fields":{"title":"Pay Attention!","date":"04 Jan 2004","slug":"/2004-01-04-pay-attention/"}},"previous":{"fields":{"title":"Otitis Media","date":"06 Jan 2004","slug":"/2004-01-06-otitis-media/"}}},{"node":{"fields":{"title":"Pay Attention!","date":"04 Jan 2004","slug":"/2004-01-04-pay-attention/","tags":"Y2004"},"html":"<p>This is what I get for not paying attention. I've been assuming that I wouldn't have a voice in deciding the Democratic presidential nominee, but now I see that Arizona, the state where I was last a resident and thus where I am registered to vote, holds their primary on February 3. And I should have requested my ballot by the end of December! I'll rush my request, and I guess I'll  have to inform myself and make up my mind after all... Dean or Clark or someone else? The last time I did this was in 1988 in the precinct caucuses in Minnesota. Back then I was a Jackson (as in Jesse) delegate. Times have changed.</p>"},"next":{"fields":{"title":"Basement Nostalgia","date":"04 Jan 2004","slug":"/2004-01-04-basement-nostalgia/"}},"previous":{"fields":{"title":"Federal Voting Assistance Program (FVAP)","date":"06 Jan 2004","slug":"/2004-01-06-federal-voting-assistance-program-fvap/"}}}],"pathPrefix":"","first":false,"last":false,"index":558,"pageCount":1101}}

